import { FormControl, FormGroup, Validators } from '@angular/forms';
import { ChangeDetectorRef, Component, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { VhAlgorithm, VhQuery, VhQueryCafe } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/cafe/components/vh-component/vh-component';
import { LanguageService } from 'src/app/cafe/services/language.service';

@Component({
  selector: 'app-manage-point',
  templateUrl: './manage-point.component.html',
  styleUrls: ['./manage-point.component.scss']
})
export class ManagePointComponent implements OnInit {
  list_customer: any = [];
  list_search_customer: any = [];
  customer_group: any = this.vhQueryCafe.getlocalCustomerClasss();
  id_customer_class: any = 'all'
  @ViewChild('modalTemplate') modalTemplate;
  public formEditPoint: FormGroup;
  isVisible = false;
  itemEdit: any
  constructor(
    private router: Router,
    private vhAlgorithm: VhAlgorithm,
    private languageService: LanguageService,
    private cdRef: ChangeDetectorRef,
    private vhQueryCafe: VhQueryCafe,
    private vhComponent:VhComponent,
  ) { }

  ngOnInit(): void {
    this.customer_group = [{ _id: 'all', name: this.languageService.translate("All") }].concat(this.vhQueryCafe.getlocalCustomerClasss())
  }

  heightScroll: string;
  ngAfterViewChecked() {
    if (document.querySelector("#product-list") && document.querySelector(".ant-table-thead") && document.querySelector(".product-list-header")) {
      this.heightScroll = document.querySelector("#product-list").clientHeight - document.querySelector(".ant-table-thead").clientHeight
        - document.querySelector(".product-list-header").clientHeight - 50 + "px";
    }
    this.cdRef.detectChanges();
  }
  goBack() {
    this.router.navigate(['/cafe/dashboard/management/sales-program/reward-points'])
  }
  /*---tìm kiếm khách hàng theo tên và email---*/
  searchCustomer(event) {
    let value = this.vhAlgorithm.changeAlias(event.toLowerCase());
    this.list_customer = this.vhAlgorithm.searchList(
      value,
      this.list_search_customer,
      ["name", "phone", "email"]
    );
  }

  /**
   * lọc khách hàng theo hạng
   */
  filterCustomer(event) {
    if(event == 'all'){
      this.list_customer = this.list_search_customer
    }
    else{
      this.list_customer = this.list_search_customer.filter(item => item.id_customer_class == event)
    }
  }
  /**
   * get dữ liệu báo cáo
   */
  getReport() {
    Promise.all([
      this.vhComponent.showLoading(''),
      this.vhQueryCafe.getEarningPointsBills(),
      this.vhQueryCafe.getEarningPointsProducts(),

    ])
      .then(([loading,earning_points_bills, earning_points_products,  ]: any) => {
        this.customer_group = [{ _id: 'all', name: this.languageService.translate("All") }].concat(this.vhQueryCafe.getlocalCustomerClasss())
        this.list_customer = this.vhQueryCafe.getlocalCustomers().filter(item => item.id_customer_class).map(i => {
          return { ...i,
            point_validity : this.vhQueryCafe.getlocalCustomerPoints(i._id),
            program_name: (this.vhQueryCafe.getlocalCustomerClasss().find(item => item._id == i.id_customer_class).id_earning_points_bill ? earning_points_bills.find(ele => ele._id == this.vhQueryCafe.getlocalCustomerClasss().find(item => item._id == i.id_customer_class).id_earning_points_bill).name : '')+ '\n' + ( this.vhQueryCafe.getlocalCustomerClasss().find(item => item._id == i.id_customer_class).id_earning_points_product ? earning_points_products.find(ele => ele._id == this.vhQueryCafe.getlocalCustomerClasss().find(item => item._id == i.id_customer_class).id_earning_points_product).name : ''),
            class_name: this.vhQueryCafe.getlocalCustomerClasss().find(ele => ele._id == i.id_customer_class).name }
        })
        this.list_search_customer = this.list_customer;
        this.filterCustomer(this.id_customer_class)
      }).finally(() => this.vhComponent.hideLoading(0))
  }


  /**
   * mở modal chỉnh sửa tích điểm
   */
  editEarningPoint(data){
    this.formEditPoint = new FormGroup({
      point: new FormControl(data.point_validity, [Validators.required]),
    })
    this.itemEdit = data;
    this.isVisible = true
  }
  /**
   * xử lý khi nhấn ok modal chỉnh sửa tích điểm
   */
  closeOkModal(){
    this.vhComponent.showLoading("", "transparent-loading", null, 0, false).then(() => {
      this.vhQueryCafe.updateCustomer_byPoints(this.itemEdit._id, this.formEditPoint.value['point']).then((bool)=>{
        if(bool){
          this.list_customer.find(item => item._id ==this.itemEdit._id).point_validity =  this.formEditPoint.value['point']
        }
      })
      this.vhComponent.hideLoading(0)
      this.isVisible = false
    })
  }
  /**
   * xử lý khi nhấn ok modal chỉnh sửa tích điểm
   */
  closeCancelModal(){
    this.isVisible = false
  }
 /** hàm sort cho các cột */
 nameCol = false;
 phoneCol = false;
 program_nameCol = false;
 class_nameCol = false;
 point_validityCol = false;
 /** Hàm thực hiện sắp xếp theo collection
  *
  * @param colName       // tên cột muôn sắp xếp
  */
 sortTable(colName){
   switch (colName) {
      case 'name':
        if(this.nameCol){
          this.list_customer = this.vhAlgorithm.sortVietnamesebyASC([...this.list_customer], colName);
        }else{
          this.list_customer = this.vhAlgorithm.sortVietnamesebyDESC([...this.list_customer], colName);
        }
        break;
      case 'phone':
        if(this.phoneCol){
          this.list_customer = this.vhAlgorithm.sortNumberbyASC([...this.list_customer], colName);
        }else{
          this.list_customer = this.vhAlgorithm.sortNumberbyDESC([...this.list_customer], colName);
        }
        break;
      case 'program_name':
        if(this.program_nameCol){
          this.list_customer = this.vhAlgorithm.sortVietnamesebyASC([...this.list_customer], colName);
        }else{
          this.list_customer = this.vhAlgorithm.sortVietnamesebyDESC([...this.list_customer], colName);
        }
        break;
      case 'class_name':
        if(this.class_nameCol){
          this.list_customer = this.vhAlgorithm.sortVietnamesebyASC([...this.list_customer], colName);
        }else{
          this.list_customer = this.vhAlgorithm.sortVietnamesebyDESC([...this.list_customer], colName);
        }
        break;
      case 'point_validity':
        if(this.point_validityCol){
          this.list_customer = this.vhAlgorithm.sortNumberbyASC([...this.list_customer], colName);
        }else{
          this.list_customer = this.vhAlgorithm.sortNumberbyDESC([...this.list_customer], colName);
        }
        break;
   }
 }
}
